@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{font-family:"Roboto",sans-serif}

.header{background-color:#6aaae1;display:flex;justify-content:space-between;align-items:flex-start}
#banner{width:100%;height:calc(100vh - 81px);background-image:url(/static/media/aprender-react.2b8ce9fc.png);background-size:cover;background-attachment:fixed;display:flex;flex-direction:column;justify-content:center;align-items:center}#banner .banner{color:#fff;text-shadow:5px 5px 5px #222;text-align:center;width:50%}#banner .banner h2{font-size:50px}#banner .banner p{font-size:20px}
div#main{display:flex;justify-content:center;align-items:center}div#main img.foto{display:flex;align-self:center;padding:10px;width:150px;border-radius:50%}div#main div.text{display:block;align-items:center;justify-content:center;padding:15px}
div#galeria{margin:60px 0;align-items:center;justify-content:center;text-align:center}div#galeria h1{display:block}div#galeria div.card{display:flex;justify-content:space-between;align-items:center;grid-gap:5px;gap:5px;text-align:center;padding:20px}div#galeria div.card div.itens{grid-gap:10px;gap:10px}div#galeria div.card div.itens img{width:50px}div#galeria div.card div.itens a{text-decoration:none;margin:5px;color:#000}
div #Contato{background-color:#6aaae1;font-size:16px;text-align:center;height:30vh;padding:20px}div #Contato a.contato img{width:30px;margin:5px}
